e se debe a la presencia de una proteína llamada mioglobina. Además de esta, también se encuentran sustancias conocidas como purinas, las cuales se descomponen en ácido úrico, un residuo del metabolismo en el cuerpo humano. Sin embargo, se ha comprobado que las carnes rojas, al contener estas sustancias en mayor proporción que otros alimentos, exigen un mayor esfuerzo del sistema digestivo. Por esta razón, su consumo excesivo puede ocasionar perjuicios para la salud.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-2 mb-md-4"},[_c('div',{staticClass:"col-lg-3 col-md-3 col-7 mb-3 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/4.svg"),"alt":""}})]),_c('div',{staticClass:"col-lg-9 col-md-9"},[_c('p',[_vm._v("Las carnes rojas contienen un alto porcentaje de grasas saturadas, lo que puede contribuir a la acumulación de colesterol de baja densidad (conocido como colesterol \"malo\") en las arterias. Esta situación incrementa el riesgo de desarrollar enfermedades cardíacas. Por esta razón, el consumo de carnes rojas es, en ocasiones, considerado un hábito poco saludable, lo que ha llevado a algunas personas a reducir su ingesta o incluso eliminar este alimento de su dieta.")])])]),_c('div',{staticClass:"row"},[_c('p',{staticClass:"mb-0"},[_vm._v("Sin embargo, las carnes rojas son una fuente valiosa de nutrientes esenciales y proteínas de alta calidad, que aportan beneficios al metabolismo y al funcionamiento general del organismo. La clave está en mantener un consumo moderado y equilibrado. El abuso en su ingesta puede generar riesgos para la salud, pero cuando se incluyen en una dieta balanceada, las carnes rojas pueden contribuir positivamente a una alimentación saludable.")])])])])])
